 <template>
  <div class="cart container">
    <div class="head flex-between">
      <p class="allNum">
        共
        <span class="red">{{list.length}}</span>件商品
      </p>
      <img src="../../assets/images/mall_cart_delete@2x.png" @click="del" />
    </div>
    <div class="list" v-for="(item, index) in list" :key="item.goods_id">
      <van-checkbox-group v-model="checked" @change="select">
        <div class="item" style="display:flex;">
          <van-checkbox class="checkbox" checked-color="#ff4c23" :name="index"></van-checkbox>
          <div class="info">
            <img :src="item.original_img" class="item" />
            <div class="introduce">
              <p class="title ellipsis">{{item.goods_name}}</p>
              <p class="text">{{item.goods_remark}}</p>
              <div class="handle">
                <p class="price">￥{{item.goods_price}}</p>
                <van-stepper v-model="item.goods_num" @change="stepperChange(item,index)" />
              </div>
            </div>
          </div>
        </div>
      </van-checkbox-group>
    </div>
    <div class="balance">
      <div class="left">
        <van-checkbox checked-color="#ff4c23" @change="all" v-model="checkAll">全选</van-checkbox>
        <p class="total">
          合计：
          <span>￥{{total}}</span>
        </p>
      </div>
      <div class="button" @click="account">去结算</div>
    </div>
  </div>
</template>

<script>
import {
  Checkbox,
  CheckboxGroup,
  Stepper,
  Dialog,
  Toast,
  CellGroup
} from "vant";
import axios from "axios";
import url from "@/axios/api.js";
export default {
  components: {
    "van-checkbox": Checkbox,
    "van-checkbox-group": CheckboxGroup,
    "van-stepper": Stepper
  },
  data() {
    return {
      checked: [],
      page: 1,
      list: [],
      finished: false,
      checkAll: false,
      total: "0.00"
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData(clear) {
      if (clear) {
        this.finished = false;
        this.page = 1;
        this.list = [];
      }
      if (this.finished) return false;
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.cartList, params).then(res => {
        this.list = res.data.data;
        if (res.data.length < 10) this.finished = true;
        this.page = ++this.page;
      });
    },
    // 勾选改变
    select() {
      this.countTotal();
    },
    // 计算价格
    countTotal() {
      let total = 0;
      this.checked.forEach((val, key) => {
        total +=
          Number(this.list[val].goods_price) * Number(this.list[val].goods_num);
      });
      this.total = total.toFixed(2);
    },
    // 全选
    all() {
      if (this.checkAll) {
        let checked = [];
        this.list.forEach((val, key) => {
          checked.push(key);
        });
        this.checked = checked;
        this.countTotal();
      } else {
        this.checked = [];
        this.countTotal();
      }
    },
    // 数量变动
    stepperChange(item, index) {
      this.countTotal();
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("id", item.goods_id);
      params.append("goods_num", 1);
      axios.post(url.changeCart, params).then(res => {
        this.countTotal();
      });
    },
    // 删除
    del() {
      if (!this.checked.length) {
        Toast("请选择要删除的商品");
        return false;
      }
      Dialog.confirm({
        title: "提示",
        message: "是否删除选中的商品！"
      }).then(() => {
        let data = new URLSearchParams();
        data.append("openid", localStorage.getItem("openid"));
        let cart_ids = [];
        this.checked.forEach((val, key) => {
          cart_ids.push(this.list[val].id);
        });
        data.append("cart_ids", cart_ids.toString());
        axios.post(url.deleteCart, data).then(res => {
          this.getData(true);
          this.total = 0;
        });
      });
    },
    // 结算
    account() {
      if (!this.checked.length) return false;
      let cart_ids = [];
      this.checked.forEach((val, key) => {
        cart_ids.push(this.list[val].id);
      });
      this.$router.push({
        path: "/submitOrder",
        query: {
          ids: cart_ids.toString(),
          action: ""
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
.van-stepper {
  margin-left: auto;
}
.head {
  padding: 10px 10px;
  font-size: 36px;
  display: flex;
  align-items: center;
  img {
    width: 39px;
    margin-left: auto;
  }
  p {
    font-size: 16px;
  }
}
.list {
  box-shadow: 0 0 16px #f0e8e8;
  margin: 0 10px;
  background: #fff;
  .checkbox {
    margin: 0 20px;
  }
  .info {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
    display: flex;
    align-items: center;
    flex: 1;
    &:last-child {
      border-bottom: 0;
    }
    img {
      width: 93px;
      height: 93px;
      border-radius: 5px;
    }
    .introduce {
      margin-left: 10px;
      flex: 1;
      .title {
        font-size: 14px;
        font-family: SourceHanSansCN-Medium;
        font-weight: 500;
        color: rgba(69, 69, 69, 1);
        line-height: 13px;
        margin: 10px 0;
      }
      .text {
        font-size: 13px;
        font-family: SourceHanSansCN-Normal;
        font-weight: 400;
        color: rgba(69, 69, 69, 1);
        line-height: 13px;
      }
      .handle {
        padding-top: 21px;
        padding-right: 10px;
        display: flex;
      }
      .price {
        color: #dc2121;
        margin-right: 20px;
      }
    }
  }
}
.balance {
  position: fixed;
  display: flex;
  height: 52px;
  bottom: 0;
  left: 0;
  background: #fff;
  width: 100%;
  box-shadow: 0 0 20px #eeecec;
  .total {
    span {
      font-size: 13px;
      color: #dc2121;
    }
  }
  .left {
    display: flex;
    padding: 0 10px;
    width: 253px;
    height: 52px;
    color: #707070;
    .van-icon-success {
      background-image: url(../../assets/images/mall_cart_empty@2x.png);
      display: block;
      width: 80px;
      height: 80px;
    }
  }
  .button {
    width: 123px;
    background: rgba(255, 76, 35, 1);
    line-height: 50px;
    text-align: center;
    font-size: 36px;
    color: #fff;
    letter-spacing: 2px;
    font-size: 17px;
  }
}
.van-checkbox__label {
  font-size: 13px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(112, 112, 112, 1);
  line-height: 13px;
}

.total {
  display: flex;
  align-items: center;
  font-size: 13px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(112, 112, 112, 1);
  line-height: 13px;
  margin-left: auto;
}
</style>


 


